video{
    position: absolute;
    width: 1035px;
    color: blue;
}

.sidebar{
    position: fixed;
    right: 0px;
    margin-left: auto;
    height: 100vh;
    width: 500px;
    background-color:#0e0c16;
    display: flex;
    flex-direction: column;
    justify-content: left;
    gap: 30px;
}

.circle,.heading,.subheading,.skill{
    position: relative;
    left: 50px;
    font-family: Roboto Condensed, Lato, Arial, sans-serif !important;
    height: 50px;
    width: 400px;
}

.projectimage{
    position: absolute;
    top: 550px;
    height: auto;
    width: 1035px;
}

.circle{
    margin-top: 30px ;
    height: 50px;
    background-color: #5918df;
    width: 50px;
    border-radius: 100px;
}

.skillset{
    font-size: 25px;
    line-height: 1.3;
    font-weight: 400;
    color: #988e9f;
    font-family: Roboto Condensed, Lato, Arial, sans-serif !important;
}

.projectname{
    position: relative;
    font-weight: 700;
    color: #f9f9f9;
    font-size: 40px;
    font-family: Roboto Condensed, Lato, Arial, sans-serif !important;
    left: 0px;
}

.matter{
    font-size: 22px;
    letter-spacing: 0.5px;
    word-spacing: 2px;
    font-weight: 100;
    line-height: 1.5;
    color: #f9f9f9;
    font-family: Roboto Condensed, Lato, Arial, sans-serif !important;
}

.visitbtn{
    color: white;
    position: relative;
    left: 50px;
    height: 70px;
    width: 250px;
    font-family: Roboto Condensed, Lato, Arial, sans-serif !important;
    margin-top: 270px;
    border-radius: 50px;
    background-color: #5918df;
    border: 0px;
}

.arrows{
    position: relative;
    top: 5px;
    left: 11px;
    color: azure;
}