html{
    background-color: #201543;
}

.navanimate{
    visibility: hidden;
}

nav{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: transparent;
    color: white;
    box-shadow: inset 0 20px 13px rgba(14, 12, 22, .85);
    z-index: -1;
}

@keyframes move-in {
    0% {
         transform: translateX(100%);
         opacity: 1; 
    }
    70% {
         transform: translateX(0%);
         opacity: 1; 
    }
    100% { 
        transform: translateX(-100%); 
        opacity: 1; 
    }
}

@keyframes move-out {
    0% {
         transform: translateX(-100%);
         visibility: visible; 
    }
    70% {
         transform: translateX(0%);
         visibility: visible; 
    }
    100% { 
        transform: translateX(100%); 
        visibility: visible; 
    }
}

.bannercontainer{
    overflow: hidden;
}

.bannerclass{
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    visibility: hidden;
    background-color: #5a06c7;
    z-index: 10;
    animation-delay: 1400ms;
}

.animationcontainer{
    position: relative;
    top: -50px;
    left: 250px;
    z-index: 10;
    overflow: hidden;
}
  
.animate {
    animation: move-in 1.5s cubic-bezier(0.81, 0.03, 0.17, 0.97) forwards;
}

.animate2 {
    animation: move-out 1.5s cubic-bezier(0.81, 0.03, 0.17, 0.97) forwards;
}

.animate-card{
    height: 100%;
